import MyComponent from '../common/mycomponent/mycomponent.vue';
import MyInput from  '../common/myinput/myinput.vue';
export default {

  components: {
    MyComponent,
    MyInput
  },
  //计算数学
  computed: {
    getPrices: function () {
      return this.prices;
    },
    getNowTime: function () {
      return Date.now();
    },
    getClass: function () {
      return {
        active: this.isActive
      }
    },
    totalPrice: function () {
      var total = 0;
      for (var i = 0; i < this.data.list.length; i++) {
        var item = this.data.list[i];
        total += item.price * item.count;
      }
      return total.toString().replace(/\B(?=(\d{3})+$)/g, ',');
    },

    getListType: function () {
    //   return Cars = [{
    //     name: '快车',
    //     type: [{
    //       name: '拼车'
    //     }, {
    //       name: '普通'
    //     }, {
    //       name: '优享'
    //     }]
    //   }, {
    //     name: '专车'
    //   }, {
    //     name: '商务车'
    //   }, {
    //     name: '顺风车'
    //   }]
   }
  },
  data() {
    return {
      prices: 0,
      isActive: false,
      isShow: true,
      flag: 1,
      Count:120,
      data: {
        list: [{
          id: 1,
          name: 'iphone x(64G)',
          price: 8180,
          count: 20
        }, {
          id: 2,
          name: 'iphone8 Plus(128G)',
          price: 6999,
          count: 21
        }, {
          id: 3,
          name: 'iphone7 Plus(64G)',
          price: 4998,
          count: 21
        }, {
          id: 3,
          name: 'Mac Pro(16G)',
          price: 13427,
          count: 21
        }]
      }
    }
  },
  created() {

  },
  mounted() {

  },

  methods: {
    //加入购物车
    onAddCart() {
      this.prices = 10;
      this.isActive = true;
    },
    setSwitch() {
      if (this.isShow) {
        this.isShow = false;
        return;
      }
      this.isShow = true;

    },
    onAdd(sales){
        console.log(sales);
    }
  }

}



